.popup {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.5);
    visibility: hidden;
    opacity: 0;
    transition: visibility 1s linear, opacity 0.5s linear;
}