.elements {
    max-width: 880px;
    margin: 0 auto;
    justify-content: center;
    padding: 50px 19px 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, 282px);
    grid-gap: 20px 17px;
}
@media screen and (max-width: 767px) {
    .elements {
        padding: 36px 19px 0;
    }
}