.popup__container {
    width: 430px;
    background-color: white;
    border-radius: 10px;
    position: relative;
    margin: 20px;
    padding: 36px;
    box-sizing: border-box;
    z-index: 10;
}
@media screen and (max-width: 767px) {
    .popup__container {
        padding: 25px 22px;
    }
}